@import url(https://fonts.googleapis.com/css2?family=Special+Elite&display=swap);
body {
    font-family: 'Special Elite', cursive;
}
a:link,
a:hover,
a:visited,
a:active {
    text-decoration: none;
    color: inherit;
}
.nav {
    height: 71px;
    box-shadow: 0px 1px 8px #888888;
    display: flex;
    align-items: center;
}

.icon {
    flex: 0.48 1;
}

.content {
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.downloadMessage {
    width: 95%;
    display: flex;
    align-items: center;
    background: #f44336;
    height: 48px;
    padding: 0 40px;
    margin: 20px 0 36px 0;
    color: white;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0;
}

.downloadText {
    flex: 1 1;
}

@media (max-width: 995px) {
    .downloadMessage {
        display: none;
    }
}

.loadMore {
    cursor: pointer;
    background-color: white;
    border: none;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0;
    padding: 7px 35px;
    font-size: 20px;
    margin-top: -20px;
}
.newsCard {
    display: flex;
    width: 100%;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    padding: 10px;
    margin-bottom: 25px;
    border-radius: 5px;
}

.newsText {
    padding: 0 0 0 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.author {
    font-size: 12px;
    font-weight: 300;
    line-height: 22px;
    color: #808290;
}

.lowerNewsText {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 1 1;
    padding: 10px 0;
}

.readmore {
    font-size: 12px;
    font-weight: 400;
    padding-top: 10px;
}

.source {
    text-decoration: none;
    color: black;
}

.newsImage {
    object-fit: cover;
    width: 320px;
    height: 268px;
}

.title {
    font-size: 22px;
    line-height: 27px;
    font-family: "Robot", sans-serif;
    /* flex-direction: column-reverse; */
}

.muted {
    font-weight: 100;
}

.description {
    font-size: 16px;
    line-height: 22px;
    color: #44444d
}

@media (max-width: 800px) {
    .newsCard {
        flex-direction: column;
        width: 100%;
    }
    .newsImage {
        width: 100%;
        height: 100%;
        padding-top: 20px;
    }
}
.footer {
    background-color: #222;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
}

.name {
    font-size: 20px;
    padding: 20px 30px 15px;
    color: white;
}

a {
    color: white;
}

.iconContainer {
    width: 60%;
    height: 50px;
    padding: 20px 0;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    font-size: 275%;
}
