.content {
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.downloadMessage {
    width: 95%;
    display: flex;
    align-items: center;
    background: #f44336;
    height: 48px;
    padding: 0 40px;
    margin: 20px 0 36px 0;
    color: white;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0;
}

.downloadText {
    flex: 1;
}

@media (max-width: 995px) {
    .downloadMessage {
        display: none;
    }
}

.loadMore {
    cursor: pointer;
    background-color: white;
    border: none;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0;
    padding: 7px 35px;
    font-size: 20px;
    margin-top: -20px;
}